<template>
    <div>
        <b-container>
            <b-row>
                <b-col>
                    메인화면<hr>
                    {{data}}
                </b-col>
            </b-row>
        </b-container>
    </div>
</template>

<script>
export default {
    name: "main",
    data(){
        return{
            data: []
        }
    },
    created: function(){
        this.$http.get(`/api/data`).then((res) =>{
            this.data = res.data
        })
    }
}
</script>